* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: #23252c;
  font-family: "Inter", sans-serif;
  background-image: url(./images/react-icon-large.png);
  background-repeat: no-repeat;
  background-size: 250px;
  background-position: right -50%;
}
.body--text {
  color: white;
}
.content-box {
  border-radius: 20px;
  position: absolute;
  width: 550px;
  left: 50%;
  top: 80%;
  transform: translate(-50%, -50%);
  background-color: #161619;
}
.img--div {
  background-color: rgb(23, 134, 134);

  border-radius: 20px 20px 0 0;
}
.nav--bar {
  text-align: center;
}
.nav--header1 {
  color: white;
  font-size: 30px;
}
.nav--header2 {
  color: #f3bf99;
  font-weight: 400;
  font-size: 18px;
}
.nav--header3 {
  color: white;
  font-weight: 400;
  font-size: 15px;
}
.btn-1,
.btn-2 {
  padding: 9px 25px;
  font-size: 15px;
  border-radius: 6px;
  border: none;
}
.btn-1 {
  background-color: white;
  font-weight: 600;
}
.btn-2 {
  background: #5093e2;
  color: white;
  font-weight: 600;
}
/* Main */
.main--content {
  color: white;
  padding: 30px 20px;
}
.main--p1,
.main--p2 {
  font-weight: 400;
  font-size: 12px;
}

/* Footer */
.footer--sec {
  background-color: rgb(10, 10, 10);
  display: flex;
  justify-content: space-around;
  padding: 20px 0 30px 0;
  border-radius: 0 0 20px 20px;
}
/* Media qauries */

@media only screen and (min-width: 240px) and (max-width: 1024px) {
  .content-box {
    width: 350px;
    transform: translate(-50%, -50%);
  }
}
